<template>
  <div class="partnerScreen">
    <div class="partnerBac">
      <div class="partContent">
        <h3>{{$t('home.partners')}}</h3>
        <p>{{$t('home.display')}}</p>
      </div>
      <div class="partImg">
        <swiper :options="swiperOptionz" ref="mySwiper1" >
          <!-- slides -->
          <swiper-slide><div class="mp"><img src="../../../images/home/part8.jpg" alt=""></div></swiper-slide>
          <swiper-slide><div class="mp"><img src="../../../images/home/part7.png" alt=""></div></swiper-slide>
          <swiper-slide><div class="mp"><img src="../../../images/home/part3.png" alt=""></div></swiper-slide>
          <swiper-slide><div class="mp"><img src="../../../images/home/part2.jpg" alt=""></div></swiper-slide>
          <swiper-slide><div class="mp"><img src="../../../images/home/part1.jpg" alt=""></div></swiper-slide>
          <swiper-slide><div class="mp"><img src="../../../images/home/part6.png" alt=""></div></swiper-slide>
          <swiper-slide><div class="mp"><img src="../../../images/home/part4.jpg" alt=""></div></swiper-slide>
          <!-- Optional controls -->
          <div class="swiper-pagination pg" slot="pagination"></div>
        </swiper>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    data(){
      return{
        swiperOptionz: {
          slidesPerView : 3,
          slidesPerGroup : 3,
          /*autoplay: {
            delay: 3000,
            stopOnLastSlide: false,
            disableOnInteraction: false,
          },*/
          // autoplay: true,
          setWrapperSize: true,
          pagination: {
            el: '.swiper-pagination'
          },
          observerParents:true,
        },
      }
    }
  }
</script>

<style lang="scss" scoped>
  @import "../../../style/mixin";
  .partnerScreen{
    width: 100%;
    padding: .48rem .3rem;
    .partnerBac {
      background: rgba(49, 49, 49, 0.8);
      border-radius: .4rem;
      .partContent{
        @include fjcc;
        h3 {
          @include sc(.3rem, #ffffff);
          padding-top: .5rem;
          padding-bottom: .2rem;
        }
        p {
          @include sc(.22rem, #ffffff);
        }
      }
      .partImg{
        padding: .4rem .3rem 0rem .3rem;
        display: flex;
        height: 150px;
        .pg{
        }
        .mp{
          display: flex;
          flex-direction: row;
          justify-content: center;
          align-content: center;
          img{
            width: 1.8rem;
            height: 1.4rem;
          }
        }
      }
    }

  }
</style>



