      </div>
    </router-link>
    <router-link to="/caseDinner">
    <div class="screen">
      <a href="#">
        <img src="../../../images/home/busParty.jpg" alt="">
        <span>{{$t('home.business')}}</span>
      </a>
    </div>
    </router-link>
    <router-link to="/caseHome">
    <div class="screen">
      <a href="#">
        <img src="../../../images/home/homeParty.jpg" alt="">
        <span>{{$t('home.familyFeast')}}</span>
      </a>
    </div>
    </router-link>
    <router-link to="/casePrivate">
    <div class="screen">
      <a href="#">
        <img src="../../../images/home/privateParty.jpg" alt="">
        <span>{{$t('home.specificEvent')}}</span>
      </a>
    </div>
    </router-link>
  </div>
</template>

<script>
  export default {
    components:{
    }
  }
</script>

<style lang="scss" scoped>
  @import "../../../style/mixin";
  .screenContainer{
    .screen{
      @include wh(100%,5.2rem);
      position: relative;
      img{
        @include wh(100%,5.2rem);
      }
      span{
        @include cl;
        display: block;
        position: absolute;
        bottom: .4rem;
        background: rgba(49,49,49,0.5);
        text-align: center;
        width: 3.68rem;
        height: .8rem;
        border-radius: .5rem;
        line-height: .8rem;
        font-size: .34rem;
        color: #ffffff;
      }
    }
  }

</style>



